<template>
  <div class="container">
    <el-dialog
      :title="text + pageTitle"
      :visible.sync="dialogFormVisible"
      width="800px"
    >
      <el-form
        :model="formBase"
        :rules="ruleInline"
        ref="dataForm"
        label-width="100px"
        class="demo-dataForm"
      >
        <el-form-item label="文章标题:" prop="title" size="small">
          <el-input
            v-model="formBase.title"
            placeholder="请输入文章标签"
          ></el-input>
        </el-form-item>
        <el-form-item label="文章内容:" prop="articleBody">
          <quill-editor
            class="quill-editor"
            v-model="formBase.articleBody"
            :options="editorOption"
          ></quill-editor>
        </el-form-item>

        <el-form-item label="视频地址" prop="videoURL" size="small">
          <el-input
            v-model="formBase.videoURL"
            placeholder="请输入视频地址"
          ></el-input>
        </el-form-item>

        <el-form-item>
          <el-button @click="handleClose">取消</el-button>
          <el-button type="primary" @click="createData">确认</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import { add, update } from '@/api/hmmm/articles'
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'
export default {
  components: {
    quillEditor
  },
  props: ['text', 'pageTitle', 'formBase', 'ruleInline'],
  data() {
    return {
      dialogFormVisible: false,
      editorOption: {
        modules: {
          toolbar: [
            ['bold', 'italic', 'underline', 'strike'],
            [{ list: 'ordered' }, { list: 'bullet' }],
            ['blockquote'],
            ['code-block', 'image', 'link']
          ]
        },
        placeholder: ''
      }
    }
  },
  methods: {
    // 弹层显示
    dialogFormV() {
      this.dialogFormVisible = true
    },
    // 弹层隐藏
    dialogFormH() {
      this.dialogFormVisible = false
    },
    // 退出
    handleClose() {
      this.$emit('handleCloseModal')
    },

    // 表单提交
    createData() {
      this.$refs.dataForm.validate(valid => {
        if (valid) {
          this.$emit('handleCloseModal')
          const data = {
            ...this.formBase
          }
          if (this.formBase.id) {
            update(data).then(() => {
              this.$message({
                message: '操作成功',
                type: 'success'
              })
              this.$emit('newDataes', this.formBase)
            })
          } else {
            add(this.formBase).then(() => {
              this.$message({
                message: '操作成功',
                type: 'success'
              })
              this.$emit('newDataes', this.formBase)
            })
          }
        } else {
          this.$Message.error('*号为必填项!')
        }
      })
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.container {
  ::v-deep.ql-editor {
    height: 200px;
    width: 500px;
  }
  ::v-deep.ql-toolbar {
    height: 34px;
    padding: 0 8px;
  }
}
</style>
