<template>
  <div class="articles-container">
    <el-card class="box-card">
      <el-row>
        <!-- 搜索 -->
        <el-form
          :inline="true"
          ref="requestParameters"
          :model="requestParameters"
          class="demo-form-inline"
          size="small"
        >
          <el-form-item label="关键字" class="abc">
            <el-input
              v-model="requestParameters.keyword"
              placeholder="根据文章标题搜索"
            ></el-input>
          </el-form-item>
          <el-form-item label="状态" class="abb">
            <el-select v-model="requestParameters.state" placeholder="请选择">
              <el-option label="启用" :value="'启用' ? 1 : 0"></el-option>
              <el-option label="禁用" value="禁用"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button @click="clearHandleFilter">清除</el-button>
            <el-button type="primary" @click="handleFilter">查询</el-button>
          </el-form-item>
          <el-button
            class="filter-item fr"
            size="small"
            style="margin-left: 10px"
            type="success"
            @click="handleCreate"
          >
            <i class="el-icon-edit"></i>
            <span class="jiqiao">新增技巧</span>
          </el-button>
        </el-form>
        <el-alert
          v-if="alertText !== ''"
          :title="alertText"
          type="info"
          class="alert"
          :closable="false"
          show-icon
        ></el-alert>
      </el-row>
      <!-- 数据 -->
      <el-table
        :key="tableKey"
        :data="dataList"
        style="width: 100%"
        fit
        highlight-current-row
      >
        <el-table-column
          prop="id"
          type="index"
          label="序号"
          width="80"
          align="left"
          header-align="left"
        >
        </el-table-column>
        <el-table-column
          prop="name"
          label="文章标题"
          width="400"
          align="left"
          header-align="left"
        >
          <template slot-scope="scope">
            <span
              >{{ scope.row.title }}
              <a
                href="#"
                class="el-icon-film"
                v-if="scope.row.videoURL"
                @click=";(isPlay = true), (videoURL = scope.row.videoURL)"
              >
              </a
            ></span>
          </template>
        </el-table-column>
        <el-table-column
          prop="visits"
          label="阅读数"
          align="left"
          header-align="left"
          min-width="80"
        >
          <template slot-scope="scope">
            <span>{{ scope.row.visits }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="username"
          label="录入人"
          min-width="80"
          align="left"
          header-align="left"
        >
          <template slot-scope="scope">
            <span>{{ scope.row.username }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="date"
          label="录入时间"
          align="left"
          header-align="left"
          min-width="80"
        >
          <template slot-scope="scope">
            <span>{{ scope.row.createTime | parseTimeByString }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="state"
          :label="$t('table.state')"
          align="left"
          header-align="left"
          width="100"
        >
          <template slot-scope="scope">
            <span>{{ scope.row.state ? '已启用' : '已禁用' }}</span>
          </template>
        </el-table-column>

        <el-table-column
          label="操作"
          align="left"
          header-align="left"
          width="180"
        >
          <template slot-scope="scope">
            <el-button type="text" @click="open(scope.row.id)">预览</el-button>
            <el-button type="text" @click="btnchangeState(scope.row)">
              {{ !scope.row.state ? '启用' : '禁用' }}
            </el-button>
            <el-button
              type="text"
              :disabled="Boolean(scope.row.state)"
              @click="handleUpdate(scope.row.id)"
              >修改</el-button
            >
            <el-button
              type="text"
              :disabled="Boolean(scope.row.state)"
              @click="removeUser(scope.row.id)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!-- end -->
      <!-- 分页 -->
      <div class="pagination" v-if="Math.ceil(total/requestParameters.pagesize) > 1">
        <PageTool
          :paginationPage="requestParameters.page"
          :paginationPagesize="requestParameters.pagesize"
          :total="total"
          @pageChange="handleCurrentChange"
          @pageSizeChange="handleSizeChange"
        ></PageTool>
      </div>
      <!-- end -->
      <!-- 新增标签弹层 -->
      <component
        v-bind:is="cpt"
        ref="editArticles"
        :formData.sync="requestParameters"
        :text="text"
        :pageTitle="pageTitle"
        :formBase="formData"
        :ruleInline="ruleInline"
        v-on:newDataes="handleLoadDataList"
        v-on:handleCloseModal="handleCloseModal"
      ></component>
      <!-- end -->
      <!-- 预览弹出层 -->
      <el-dialog title="预览文章" :visible.sync="dialogVisible" width="800px">
        <el-form
          :model="articlesList"
          ref="dataForm"
          label-width="100px"
          class="demo-dataForm"
        >
          <h2>{{ articlesList.title }}</h2>
          <div class="articleHead">
            <span>{{ articlesList.createTime | parseTimeByString }}</span>
            <span>{{ username }}</span>
            <span class="el-icon-view"></span>
            <span>{{ articlesList.visits }}</span>
          </div>
          <div class="content" v-html="articlesList.articleBody"></div>
        </el-form>
      </el-dialog>
      <!-- end -->
      <!-- 视频弹出层 -->
      <div class="popup" v-if="isPlay">
        <div class="videoWrap">
          <video autoplay class="video" :src="videoURL"></video>
        </div>
        <div class="close" @click="isPlay = false">
          <i class="el-icon-close"></i>
        </div>
      </div>
      <!--  -->
    </el-card>
  </div>
</template>

<script>
import { list, remove, detail, changeState } from '@/api/hmmm/articles'
import PageTool from '../../module-manage/components/page-tool.vue'
import ArticlesAdd from '../components/articles-add.vue'
import { detail as getUserInfo } from '@/api/base/users.js'
export default {
  components: {
    PageTool,
    ArticlesAdd
  },
  props: {},
  data() {
    return {
      videoURL: '',
      cpt: 'ArticlesAdd',
      articlesList: {},
      isPlay: false,
      username: '',
      dialogVisible: false,
      alertText: '',
      total: null,
      tableKey: 0,
      deletedDate: false,
      showDate: true,
      dialogStatus: '',
      pageTitle: '文章', // 页面标题
      text: '', // 新增、编辑文本
      dataList: [],
      requestParameters: {
        page: 1,
        pagesize: 10,
        keyword: '',
        state: null
      },
      formData: {
        title: '',
        articleBody: '',
        videoURL: ''
      },
      ruleInline: {
        // 表单验证
        title: [
          { required: true, message: '请输入文章标签', trigger: 'blur' }
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' },
        ],
        articleBody: [
          { required: true, message: '请输入文章内容', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {},
  watch: {},
  created() {
    // 读取数据
    this.getList()
    // 键盘enter操作
    var lett = this
    document.onkeydown = function(e) {
      var key = window.event.keyCode
      if (key === 13) {
        lett.handleFilter(this.requestParameters)
      }
    }
  },
  mounted() {},
  methods: {
    // 切换状态
    async btnchangeState(row) {
      const state = row.state ? 0 : 1
      try {
        await changeState({
          id: row.id,
          state
        })
        row.state = state
        this.$message({
          message: '操作成功',
          type: 'success'
        })
      } catch (err) {
        console.log(err)
      }
    },

    // 预览
    open(id) {
      this.formData.id = id
      this.dialogVisible = true
      // console.log(id)
      detail({ id }).then((data, err) => {
        this.articlesList = data.data
        getUserInfo({ id: data.data.creatorID }).then(data => {
          this.username = data.data.username
        })
      })
      // const data =await detail(id)
      // console.log(data);
    },
    // 获取列表数据
    async getList() {
      const { data } = await list(this.requestParameters)
      console.log(data)
      this.dataList = data.items
      this.total = data.counts
      this.alertText = `数据一共 ${this.total} 条`
    },
    // 搜索信息
    handleFilter() {
      this.requestParameters.page = 1
      console.log(this.requestParameters.state)
      if (this.requestParameters.state === '启用') {
        this.requestParameters.state = 1
      }

      this.getList(this.requestParameters)
    },
    // 清除
    clearHandleFilter() {
      if (
        this.requestParameters.keyword.length === 0 &&
        this.requestParameters.state.length === 0
      ) {
        return
      }
      this.requestParameters.keyword = ''
      this.requestParameters.state = null
      // this.dataList = ''
      // this.total = 0
      // this.alertText = `数据一共 ${this.total} 条`
    },
    // 每页显示信息条数
    handleSizeChange(val) {
      this.requestParameters.pagesize = val
      if (this.requestParameters.page === 1) {
        this.getList(this.requestParameters)
      }
    },
    // 进入某一页
    handleCurrentChange(val) {
      this.requestParameters.page = val
      this.getList()
    },
    rowClassStatus(row) {
      if (row.row.is_deleted === 1) {
        this.deletedDate = true
        this.showDate = false
        return 'disabled'
      } else {
        return ''
      }
    },
    // 新增学科刷新列表
    handleLoadDataList() {
      this.getList()
    },
    query() {
      this.formData = {
        title: '',
        articleBody: '',
        videoURL: ''
      }
    },
    // 新增技巧
    handleCreate() {
      this.query()
      this.text = '新增'
      this.$refs.editArticles.dialogFormV()
    },
    // 窗口操作**********************************
    // 弹框关闭
    handleCloseModal() {
      this.$refs.editArticles.dialogFormH()
    },
    // 编辑
    // 表单详情数据加载
    hanldeEditForm(objeditId) {
      this.formData.id = objeditId
      detail({ id: objeditId }).then((data, err) => {
        console.log(data)
        var datalist = data.data
        if (err) {
          return err
        }
        this.formData.id = datalist.id
        this.formData.title = datalist.title
        this.formData.articleBody = datalist.articleBody
        this.formData.videoURL = datalist.videoURL
      })
    },
    handleUpdate(objeditId) {
      this.query()
      var _this = this
      this.text = '修改'
      this.$refs.editArticles.dialogFormV()
      _this.hanldeEditForm(objeditId)
    },

    // 删除
    removeUser(user) {
      this.$confirm('此操作将永久删除学科 ' + ', 是否继续?', '提示', {
        type: 'warning'
      })
        .then(() => {
          remove({ id: user })
            .then(response => {
              this.$message.success('成功删除了学科' + '!')
              this.dataList.splice(user, 1)
              this.getList(this.requestParameters)
            })
            .catch(response => {
              this.$message.error('删除失败!')
            })
        })
        .catch(() => {
          this.$message.info('已取消操作!')
        })
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.articles-container {
  padding: 10px 0;
  margin: 0 10px;
  .alert {
    margin-bottom: 15px;
  }
  .pagination {
    margin-top: 10px;
    text-align: right;
  }

  .el-card__body {
    padding: 20px;
  }
  .el-icon-film {
    color: #00f;
    font-size: 18px;
  }
  .jiqiao {
    font-size: 12px;
    color: #fff;
  }
  .abc {
    margin-left: 20px;
  }
  .abb {
    margin-left: 40px;
  }
  .articleHead,
  span {
    margin-left: 10px;
    margin-bottom: 10px;
    color: #606266;
    font-size: 14px;
  }
  .content {
    background: #f5f5f5;
    padding: 10px;
    border-top: 1px dashed #ccc;
  }
  .popup {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2000;
    background-color: rgba(0, 0, 0, 0.3);
    .videoWrap {
      position: absolute;
      left: 50%;
      top: 50%;
      width: 800px;
      height: 500px;
      transform: translate(-50%, -50%);
      z-index: 2001;
      .video {
        width: 100%;
      }
    }
    .close {
      position: absolute;
      left: 50%;
      top: 20px;
      width: 50px;
      height: 50px;
      transform: translateX(-50%);
      border-radius: 50%;
      background-color: rgba(0, 0, 0, 0.5);
      text-align: center;
      line-height: 50px;
      color: #fff;
      font-size: 20px;
    }
  }
  ::v-deep .ql-editor {
    width: 100%;
  }
}
</style>
